import React from "react";
// ;
import { AddCartAction } from "../js/actions/cartaction";
import { useDispatch, useSelector } from "react-redux";
// reactstrap components
import {
  Container,
  Row,
  Col,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Button,
  Pagination,
  PaginationItem,
  PaginationLink,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap";

import "react-accessible-accordion/dist/fancy-example.css";

// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import { Link, useLocation } from "react-router-dom";
import ProductFilter from "../components/ProductFilter/ProductFilter";
import axios from "axios";
import { useParams } from "react-router-dom";
import currency from "../js/methods/currency";
import { toast } from "react-toastify";
import  defaultImage from 'assets/img/default-avatar.png';

function StoreDetails() {
  const location = useLocation();
  const token = localStorage.getItem("user-token");
  const [storeListing, setstoreListing] = React.useState([]);
  const [filteredProducts, setfilteredProducts] = React.useState([]);
  const [categories, setCategories] = React.useState([]);
  const params = useParams();
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.userCart).items;
  const [id, setid] = React.useState("all");
  const [store, setstore] = React.useState({});
  const [category, setcategory] = React.useState(' Categories')
  const [brands, setbrands] = React.useState([])
  const [brand, setbrand] = React.useState(' Brands');
  const [brandid, setbrandid] = React.useState(null);
  const [sortvalue, setsortvalue] = React.useState('category')
   const [pagination, setpagination] = React.useState({});
   var [currentpage, setcurrentpage] = React.useState(1);
   const [pages, setpages] = React.useState([]);
   const [lastpage, setlastpage] = React.useState(null);
 const [loading, setloading] = React.useState(true)

  React.useEffect(() => {
    var data = {
      store_id: params.store_id,
    };
    axios
      .post(
        `${process.env.REACT_APP_URL}/api/store/products/all?page=${currentpage}`,
        data
      )
      .then((res) => {
        if (res.status === 200) {
          setstoreListing(res.data.data);
          // setfilteredProducts(res.data);
          setcurrentpage(res.data.meta.current_page);
          setlastpage(res.data.meta.last_page);
setloading(false)
          var info = {
            current_page: res.data.meta.current_page,
            from: res.data.meta.from,
            last_page: res.data.meta.last_page,
            per_page: res.data.meta.per_page,
            to: res.data.meta.to,
            total: res.data.meta.total,
          };
          setpagination(info);

          if (res.data.meta.total) {
            var pages = [];
            for (
              let i = 1;
              i <= Math.ceil(res.data.meta.total / res.data.meta.per_page);
              i++
            ) {
              pages.push(i);
            }

            setpages(pages);
          }
        }
      });




  }, [params.store_id,currentpage]);

  React.useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_URL}/api/categories/${params.store_id}`)
      .then((res) => {
        if (res.status === 200) {
          setCategories(res.data);
        }
      });

    axios
      .get(`${process.env.REACT_APP_URL}/api/stores/${params.store_id}`)
      .then((res) => {
        if (res.status === 200) {
          setstore(res.data);
        }
      });

    axios
      .get(`${process.env.REACT_APP_URL}/api/brands/${params.store_id}`)
      .then((res) => {
        if (res.status === 200) {
          setbrands(res.data);
        }
      });

  }, [params.store_id]);

  React.useEffect(() => {
    const queryParams = new URLSearchParams(location.search);

    if (queryParams.get("category")) {
      handleCategories(queryParams.get("category"));
    }

    return () => {};
  }, [location.search]);

  React.useEffect(() => {
    var result;
    if (sortvalue === "category") {
      if (id === "all") {
        setfilteredProducts(storeListing);
        return;
      }
       result = storeListing.filter(
        (item) => Number(item.category_id) === Number(id)
      );
      setfilteredProducts(result);
    }
    if (sortvalue === "brand") {
      if (brandid === "all") {
        setfilteredProducts(storeListing);
        return;
      }
       result = storeListing.filter(
        (item) => Number(item.brand_id) === Number(brandid)
      );
      setfilteredProducts(result);
    }
    return function cleanup() {};
  }, [id, storeListing, brandid, sortvalue]);

  function handleSearch(e) {
    var value = e.target.value;

    var result = storeListing.filter((item) =>
      item.product_name.toLowerCase().includes(value.toLowerCase())
    );
    setfilteredProducts(result);
  }

  function handlesetsortvalue(val){

    setsortvalue(val)
    setfilteredProducts(storeListing);
    setid("all");
    setbrandid("all");
  }
  function handleCategories(id,name) {

    setid(id);
    setcategory(name)
  }
  function handleBrands(id, name) {

    setbrandid(id);
    setbrand(name);
  }


  function addToCart(product_id, store_id) {
    if (!token) {
      toast.info("Login to complete action");
      return;
    }
    var data = {
      product_id,
      store_id,
    };
    dispatch(AddCartAction(data));
  }
  function isInCart(product_id, store_id) {
    if (!cart.length) return;
    return cart.some(
      (item) =>
        Number(item.product_id) === Number(product_id) &&
        Number(item.store_id) === Number(store_id)
    );
  }
   function handlecurrentpage(val) {
     setcurrentpage(val);
   }
   function handleNext() {
     if (currentpage === lastpage) return;
     var current = currentpage;
     current++;
     setcurrentpage(current);
   }

   function handlePrev() {
     if (currentpage === 1) return;
     var current = currentpage;
     current--;
     setcurrentpage(current);
   }
  let renderedpages = pages.map((page, index) => (
    <PaginationItem key={index} active={currentpage === page}>
      <PaginationLink onClick={() => handlecurrentpage(page)}>
        {page}
      </PaginationLink>
    </PaginationItem>
  ));


  const [rightFocus, setRightFocus] = React.useState(false);
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);
  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        {!loading ? (
          <section className="section">
            <Container>
              <Breadcrumb className="custom-breadcrumb px-0">
                <BreadcrumbItem className="px-0">
                  <Link to={`/stores-listing`}>
                    Stores
                  </Link>
                </BreadcrumbItem>

                <BreadcrumbItem active>{store.name}</BreadcrumbItem>
              </Breadcrumb>
              <Row>
                <Col className="ml-auto mr-auto text-center" lg="8">
                  <div className="mb-5">
                    <h4 className="font-weight-bold text-capitalize">
                      {store.name}, {store.location}
                    </h4>
                    <p>
                      Showing all products in stock at this store. Search for
                      any item or filter through categories
                    </p>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col className="ml-auto mr-auto text-center" lg="6">
                  <InputGroup className={rightFocus ? "input-group-focus" : ""}>
                    <Input
                      placeholder="Search for product names"
                      type="search"
                      onChange={(e) => handleSearch(e)}
                      onFocus={() => setRightFocus(true)}
                      onBlur={() => setRightFocus(false)}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <i className="fa fa-search" />
                      </InputGroupText>
                    </InputGroupAddon>
                  </InputGroup>
                </Col>
              </Row>

              <>
                <Row>
                  <Col>
                    <ProductFilter
                      id={id}
                      storecategories={categories}
                      handleCategories={handleCategories}
                      handlesetsortvalue={handlesetsortvalue}
                      category={category}
                      brands={brands}
                      brand={brand}
                      handleBrands={handleBrands}
                      brandid={brandid}
                    />
                  </Col>
                </Row>
                {filteredProducts.length ? (
                  <Row>
                    {filteredProducts.map((item, id) => (
                      <Col
                        className="product-listing mb-4 "
                        lg="3"
                        md="4"
                        sm="6"
                        xs="6"
                        key={id}
                      >
                        <Link
                          to={`/product-details/${item.id}`}
                          className="d-block"
                        >
                          <div className="product-block">
                            <p className="product-location">
                              <i className="fa fa-map-marker-alt mr-2" />
                              {item.store.name}
                            </p>
                            <img
                              alt="..."
                              className="product-img"
                              src={item.image ? item.image[0] : defaultImage}
                            />
                          </div>

                          <p className="product-name">{item.product_name}</p>
                        </Link>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="product-price h4">
                            {item.sales_price ? (
                              <span className="miniprice">
                                {currency(item.price)}
                              </span>
                            ) : (
                              ""
                            )}
                            <br />
                            {currency(
                              item.sales_price ? item.sales_price : item.price
                            )}
                          </p>
                          {!isInCart(item.id, item.store.id) && item.in_stock ? (
                            <Button
                              onClick={() => addToCart(item.id, item.store.id)}
                              className="btn-icon btn-round my-0 my-1"
                              disabled={
                                isInCart(item.id, item.store.id) ? true : false
                              }
                              color="success"
                              type="button"
                              size="sm"
                            >
                              <i className="now-ui-icons shopping_cart-simple" />
                            </Button>
                          ) : (
                            <Button
                              className="btn-icon btn-round text-dark  my-0 my-1"
                              color="secondary"
                              type="button"
                              size="sm"
                            >
                              <i className="now-ui-icons shopping_cart-simple" />
                            </Button>
                          )}
                        </div>
                      </Col>
                    ))}
                  </Row>
                ) : (
                  <div className="text-center mt-5">
                    <p>No product available</p>
                  </div>
                )}

                {filteredProducts.length === 30 ? (
                  <Row>
                    <Col className="d-flex justify-content-center mt-4">
                      <Pagination aria-label=" navigation ">
                        <PaginationItem>
                          <PaginationLink
                            first
                            onClick={() => handlecurrentpage(1)}
                          />
                        </PaginationItem>
                        <PaginationItem>
                          <PaginationLink
                            onClick={() => handlePrev()}
                            previous
                          />
                        </PaginationItem>
                        {renderedpages}
                        <PaginationItem>
                          <PaginationLink onClick={() => handleNext()} next />
                        </PaginationItem>
                        <PaginationItem>
                          <PaginationLink
                            onClick={() =>
                              handlecurrentpage(pagination.last_page)
                            }
                            last
                          />
                        </PaginationItem>
                      </Pagination>
                    </Col>
                  </Row>
                ) : (
                  ""
                )}
              </>
            </Container>
          </section>
        ) : (
          <section
            style={{ height: "80vh" }}
            className="p-5 d-flex justify-content-center align-items-center"
          >
            <span className="text-center">
              <i
                className="fa fa-spinner fa-spin fa-4x text-primary"
                aria-hidden="true"
              ></i>
              <br />
              <br />
              <span>Loading products...</span>
            </span>
          </section>
        )}

        <DefaultFooter />
      </div>
    </>
  );
}

export default StoreDetails;
