import * as ActionTypes from "../constants/action-types";
import { toast } from "react-toastify";

import {
  SendPasswordResetService,
  RegisterUserService,
  LoginUserService,
  LogOutUserService,
  UpdatePasswordService,
} from "../services/authservice";

export const RegisterAction = (credentials) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.RESTART_AUTH_RESPONSE });
    dispatch({ type: ActionTypes.LOADING });
    RegisterUserService(credentials).then(
      (res) => {
        if (res.hasOwnProperty("success") && res.success === true) {
          dispatch({ type: ActionTypes.SIGNUP_SUCCESS, res });
        } else if (res.hasOwnProperty("success") && res.success === false) {
          dispatch({ type: ActionTypes.SIGNUP_ERROR, res });
        }
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};
export const LoginAction = (credentials, history) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.RESTART_AUTH_RESPONSE });
    dispatch({ type: ActionTypes.LOADING });
    LoginUserService(credentials).then(
      (res) => {

        if (res.hasOwnProperty("success") && res.success === true) {
          localStorage.setItem("user-token", res.token);
          localStorage.setItem("payviame-token", res.payviame_token);
          localStorage.setItem("user-data", JSON.stringify(res.data));
          dispatch({ type: ActionTypes.LOGIN_SUCCESS, res });
          toast.success("Successfully logged in");
          history.push("/");
        } else if (res.hasOwnProperty("success") && res.success === false) {

          dispatch({ type: ActionTypes.LOGIN_ERROR, res });
        }
        else{

           dispatch({ type: ActionTypes.LOGIN_ERROR, res});
        }
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};
export const LogoutAction = (history, toast) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.RESTART_AUTH_RESPONSE });
    LogOutUserService(history, toast).then(
      (res) => {
        if (res.hasOwnProperty("success") && res.success === true) {
          localStorage.removeItem("payviame-token");
          dispatch({ type: ActionTypes.LOGOUT_SUCCESS });
          dispatch({ type: ActionTypes.RESTART_AUTH_RESPONSE });
        } else if (res.hasOwnProperty("success") && res.success === false) {
          dispatch({ type: ActionTypes.LOGOUT_ERROR, res });
        }
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const SendPasswordResetAction = (email) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.RESTART_AUTH_RESPONSE });
    SendPasswordResetService(email).then((res) => {
      if (res.hasOwnProperty("success") && res.success === true) {
        dispatch({ type: ActionTypes.SEND_PASSWORD_RESET_SUCCESS, res });
      }
      if (res.hasOwnProperty("success") && res.success === false) {
        dispatch({ type: ActionTypes.SEND_PASSWORD_RESET_ERROR, res });
      }
    });
  };
};

