import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  Form,
  FormGroup,
  Input,
  FormText,
  Container,
  Col,
  Row,
} from "reactstrap";

import { useHistory, Link } from "react-router-dom";
import authImage from "assets/img/illustrations/auth-img.png";
// core components
import DefaultNavbar from "../../components/Navbars/DefaultNavbar";
import { useDispatch } from "react-redux";
import {
  RegisterAction,
  RestartAuthResponse,
} from "../../js/actions/authaction";

function SignUpPage() {
  const dispatch = useDispatch();
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [passwordValid, setPasswordValid] = useState(true);
  const token = localStorage.getItem("user-token");
  const history = useHistory();
  const authResponse = useSelector((state) => state.userAuth.authResponse);
  const [isClicked, setisClicked] = useState(false);

  useEffect(() => {
    if (token) {
      history.push("/home");
    }
    return () => {};
  }, [history, token]);

  useEffect(() => {
    if (authResponse.success && authResponse.message === "login successful") {
      history.push("/login");
    }
    if (
      !authResponse.success &&
      authResponse.message === "something went wrong"
    ) {
      setisClicked(false);
      if (authResponse.error.email) {
        toast.error(authResponse.error.email[0]);
      }
      if (authResponse.error.phoneNumber) {
        toast.error(authResponse.error.phoneNumber[0]);
      }
      dispatch(RestartAuthResponse());
    }
    return () => {
      dispatch(RestartAuthResponse());
    };
  }, [authResponse, history, dispatch]);

  useEffect(() => {
    document.body.classList.add("login-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("login-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  function handleFirstName(e) {
    setFirstName(e);
  }
  function handleLastName(e) {
    setLastName(e);
  }
  function handleEmail(e) {
    setEmail(e);
  }

  function handlePhone(e) {
    setPhone(e);
  }
  function handlePassword(e) {
    setPassword(e);
    if (e.length > 6) {
      setPasswordValid(true);
    } else {
      setPasswordValid(false);
    }
  }
  function handleSubmit(e) {
    e.preventDefault();
    let data = {
      firstName: firstName,
      lastName: lastName,
      email: email,
      phoneNumber: phone,
      password: password,
    };

    dispatch(RegisterAction(data));
  }
  function handleSocialLogin(provider) {
    var url = `${process.env.REACT_APP_URL}/api/auth/${provider}/redirect`;
    window.open(
      url,
      "Social_Login",
      "toolbar=no,scrollbars=no,location=no,statusbar=no,menubar=no,resizable=0,width=100,height=100,left = 490,top = 262"
    );
  }

  return (
    <>
      <DefaultNavbar />
      <div className="page-header">
        <div className="content">
          <Container>
            <Row>
              <Col
                className="ml-auto mr-auto mt-4 mt-md-0 mb-3 text-center"
                md="8"
              >
                <h4 className="text-primary" style={{ fontWeight: "600" }}>
                  Signup with EnterMarket
                </h4>
              </Col>
            </Row>
            <Row className="justify-content-center align-items-center">
              <Col lg="6" className="d-none d-md-block">
                <img alt="..." className="img-fluid" src={authImage} />
              </Col>
              <Col className="mt-4 mt-md-0 " lg="4">
                <Card className="card-login card-plain">
                  <Form onSubmit={handleSubmit} className="form">
                    <CardBody>
                      <Row>
                        <div className="col">
                          <FormGroup>
                            <label htmlFor="firstName">First name</label>
                            <Input
                              id="firstName"
                              required
                              placeholder="First name"
                              type="text"
                              onChange={(e) => handleFirstName(e.target.value)}
                              value={firstName}
                            />
                          </FormGroup>
                        </div>
                        <div className="col">
                          <FormGroup>
                            <label htmlFor="lastName">Last name</label>
                            <Input
                              required
                              id="lastName"
                              placeholder="Last name"
                              type="text"
                              onChange={(e) => handleLastName(e.target.value)}
                              value={lastName}
                            />
                          </FormGroup>
                        </div>
                      </Row>
                      <FormGroup>
                        <label htmlFor="exampleInputEmail1">
                          Email address
                        </label>
                        <Input
                          required
                          aria-describedby="emailHelp"
                          id="exampleInputEmail1"
                          placeholder="Enter email"
                          type="email"
                          onChange={(e) => handleEmail(e.target.value)}
                          value={email}
                        />
                      </FormGroup>
                      <FormGroup>
                        <label htmlFor="mobileNumber">Mobile Number</label>
                        <Input
                          required
                          id="mobileNumber"
                          placeholder="0123456789"
                          type="text"
                          valuedefault="+234"
                          onChange={(e) => handlePhone(e.target.value)}
                          value={phone}
                        />
                      </FormGroup>
                      <FormGroup>
                        <label htmlFor="exampleInputPassword1">Password</label>
                        <Input
                          required
                          id="exampleInputPassword1"
                          placeholder="Password"
                          type="password"
                          onChange={(e) => handlePassword(e.target.value)}
                          value={password}
                        />
                        {passwordValid ? (
                          ""
                        ) : (
                          <FormText
                            className="text-muted"
                            color="danger"
                            id="emailHelp"
                          >
                            The password should be up to 6 characters
                          </FormText>
                        )}
                      </FormGroup>
                      <Button
                        className="bg-primary-gradient"
                        color="primary"
                        type="submit"
                        size="lg"
                        block
                        disabled={isClicked}
                      >
                        Sign Up{" "}
                        {isClicked ? (
                          <i
                            className="fa fa-spinner fa-spin"
                            aria-hidden="true"
                          ></i>
                        ) : (
                          ""
                        )}
                      </Button>
                      <div className="text-center mt-4">
                        <p className="form-text mb-0">
                          Or signup with your registered social pages
                        </p>
                      </div>
                    </CardBody>
                    <CardFooter className="text-center btn-social">
                      <Button
                        block
                        className="google-btn"
                        color="danger"
                        type="button"
                        onClick={(e) => handleSocialLogin("google")}
                        size="lg"
                      >
                        <i className="fab fa-google-plus-g" />
                        Signup with Gmail
                      </Button>
                      <Button
                        block
                        className="facebook-btn"
                        color="info"
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                        size="lg"
                      >
                        <i className="fab fa-facebook-f" />
                        Signup with Facebook
                      </Button>
                      <div className="text-center mt-5">
                        <p>
                          Already registered?{" "}
                          <Link to="/login" style={{ fontWeight: "600" }}>
                            Login
                          </Link>
                        </p>
                      </div>
                    </CardFooter>
                  </Form>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </>
  );
}

export default SignUpPage;
