function CheckOut() {
  const [modal, setModal] = React.useState(false);
  const toggle = () => setModal(!modal);

  const cartItems = useSelector((state) => state.userCart.items);
  const total = useSelector((state) => state.userCart.total);
  const commission = useSelector((state) => state.userCart.commission);
  const weight = useSelector((state) => state.userCart.weight);
  const shipping = useSelector((state) => state.userCart.shipping);
  const fulfilment = useSelector((state) => state.userCart.fulfilment);
  const profileDetails = useSelector((state) => state.userDetails.userProfile);
  const [cities, setcities] = useState([]);
  const dispatch = useDispatch();
  const token = localStorage.getItem("user-token");
  const [loading, setloading] = useState(false);
  const [detail, setDetail] = useState({
    name: "",
    surname: "",
    firstName: "",
    address: "",
    city: "",
    state: "",
    pickupPoint: "",
    phoneNumber: "",
    email: "",
    extraInstruction: "",
    // paymentMethod: "",
    policy: false,
    coupon: "",
    isScheduled: false,
    schedule_time: new Date(),
  });
  const [name, setname] = useState("");
  const [hidepaybutton, sethidepaybutton] = React.useState(false);
  const [showdetails, setshowdetails] = useState("desktop");
  const [showcart, setshowcart] = useState(false);

  React.useEffect(() => {
    if (window.innerWidth < 768) {
      setshowdetails("mobile");
    } else {
      setshowdetails("desktop");
    }
  }, []);
  const history = useHistory();
  React.useEffect(() => {
    if (!token) {
      history.push("/login");
    }
    return () => {};
  }, [history, token]);

  React.useEffect(() => {
    if (!cartItems.length) {
      sethidepaybutton(true);
    } else {
      sethidepaybutton(false);
    }
    return () => {};
  }, [cartItems]);
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);
  React.useEffect(() => {
    setDetail({
      surname: profileDetails.lastName,
      firstName: profileDetails.firstName,
      address: profileDetails.address ? profileDetails.address : "",
      city: profileDetails.city ? profileDetails.city : "",
      state: profileDetails.state ? profileDetails.state : "",
      pickupPoint: "",
      phoneNumber: profileDetails.phoneNumber ? profileDetails.phoneNumber : "",
      email: profileDetails.email,
      extraInstruction: "",
      // paymentMethod: "",
      schedule_time: new Date(),
      policy: false,
      coupon: "",
      shippingtype: "standard",
      deliverymethod: "home",
      isScheduled: false,
    });
  }, [profileDetails]);

  function handleAdd(id) {
    var data = {
      action: "plus",
    };
    dispatch(UpdateCartAction(id, data));
  }
  function handleSubtract(id) {
    var data = {
      action: "subtract",
    };

    dispatch(UpdateCartAction(id, data));
  }
  function handleName(e) {
    setname(e);
  }
  function handleOnChange(e) {
    const value = e.target.value;

    setDetail({
      ...detail,
      [e.target.name]: value,
    });
  }
  function handlePolicy(e) {
    setDetail({
      ...detail,
      policy: e,
    });
  }
  function handleShippingType(e) {
    setDetail({
      ...detail,
      shippingtype: e.target.value,
    });
  }
  function handleDeliveryType(e) {
    setDetail({
      ...detail,
      deliverymethod: e.target.value,
    });
  }
  function handleScheduleTime(e) {
    setDetail({
      ...detail,
      schedule_time: e.target.value,
    });
  }
  React.useEffect(() => {
    if (!detail.state) {
      return;
    }
    if (states.length) {
      var cities = states.find((item) => {
        return item.state.name.toLowerCase() === detail.state.toLowerCase();
      });
      if (cities) {
        setcities(cities.state.locals);
      }
    }
  }, [detail.state]);

  function handleSubmit(e) {
    e.preventDefault();
    setloading(true);
    if (!detail.policy) {
      toast.info("Agree to our policy");
      setloading(false);
      return;
    }

    detail.commission = commission;
    detail.shipping = shipping;
    if (detail.shippingtype === "schedule") {
      detail.isScheduled = true;
    } else {
      detail.isScheduled = false;
    }

    detail.title = "Order";
    detail.name = name;
    axios
      .post(`${process.env.REACT_APP_URL}/api/user/orders`, detail, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 201) {
          window.location.href = res.data.data.authorization_url;
          setloading(false);
        }
      })
      .catch((err) => {
        setloading(false);
        toast.error(err.respnse.data.message);
      });
  }

  function deleteCart(id) {
    dispatch(RemoveCartAction(id));
  }
  function handleCart() {
    setshowcart(!showcart);
  }

  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <section className="section section-checkout">
          <Container>
            {showdetails === "desktop" ? (
              <Row className="flex-column-reverse flex-md-row">
                <Col lg="7">
                  <Form
                    className="checkout-form"
                    onSubmit={(e) => handleSubmit(e)}
                  >
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Personal Details</div>
                      </Col>
                      <Col lg="9">
                        <div className="form-row">
                          <FormGroup className="col-lg-5">
                            <label>Surname</label>
                            <Input
                              placeholder="Surname"
                              defaultValue={detail.surname}
                              readOnly
                              type="text"
                              name="surname"
                            />
                          </FormGroup>
                          <FormGroup className="col-lg-5">
                            <label>First Name</label>
                            <Input
                              placeholder="First Name"
                              defaultValue={detail.firstName}
                              readOnly
                              type="text"
                              name="firstName"
                            />
                          </FormGroup>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Shipping Details</div>
                      </Col>
                      <Col lg="9">
                        <div>
                          <div>
                            <FormGroup check>
                              <Label check className="delivery">
                                <Input
                                  type="radio"
                                  name="deliverymethod"
                                  checked={detail.deliverymethod === "home"}
                                  value="home"
                                  onChange={(e) => handleDeliveryType(e)}
                                />{" "}
                                <span>Have it delivered to your home</span>
                              </Label>
                            </FormGroup>

                            <div
                              className="form-row"
                              style={{
                                opacity:
                                  detail.deliverymethod === "pickup" ? 0.4 : "",
                              }}
                            >
                              <FormGroup className="col-lg-10">
                                <label>Address</label>
                                <Input
                                  placeholder="Address"
                                  value={detail.address}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  type="text"
                                  name="address"
                                />
                              </FormGroup>
                              <FormGroup className="col-lg-5">
                                <label>State</label>
                                <Input
                                  placeholder="State"
                                  value={detail.state}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  type="select"
                                  name="state"
                                >
                                  <option value="">Choose...</option>
                                  {states.map((item) => (
                                    <option
                                      value={item.state.name}
                                      key={item.state.id}
                                    >
                                      {item.state.name}
                                    </option>
                                  ))}
                                </Input>
                              </FormGroup>

                              <FormGroup className="col-lg-5">
                                <label>City</label>
                                <Input
                                  placeholder="City"
                                  value={detail.city}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  type="select"
                                  name="city"
                                >
                                  <option value="">Choose...</option>
                                  {cities.map((item) => (
                                    <option value={item.name} key={item.id}>
                                      {item.name}
                                    </option>
                                  ))}
                                </Input>
                              </FormGroup>
                            </div>
                          </div>

                          <div className="mb-4">
                            <FormGroup check>
                              <Label check className="delivery">
                                <Input
                                  type="radio"
                                  name="deliverymethod"
                                  checked={detail.deliverymethod === "pickup"}
                                  value="pickup"
                                  onChange={(e) => handleDeliveryType(e)}
                                />{" "}
                                <span>
                                  Or have it dropped at a pick up location
                                </span>
                              </Label>
                            </FormGroup>
                            <div
                              style={{
                                opacity:
                                  detail.deliverymethod === "home" ? 0.4 : "",
                              }}
                            >
                              <div className="form-row">
                                <FormGroup className="col-lg-10">
                                  <label>
                                    Select pick up point nearest to you
                                  </label>
                                  <Input
                                    placeholder="PickUp Point"
                                    value={detail.pickupPoint}
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    name="pickupPoint"
                                    type="select"
                                  >
                                    <option defaultValue="">
                                      4, Maijala street, Ikeja GRA, Ikeja, Lagos
                                    </option>
                                  </Input>
                                </FormGroup>
                              </div>
                            </div>
                          </div>

                          <div className="mb-4">
                            <p className="mb-0">Shipping type</p>
                            <div className="d-flex">
                              <FormGroup check className="mr-3">
                                <Label check className="shipping">
                                  <Input
                                    checked={detail.shippingtype === "standard"}
                                    type="radio"
                                    name="shippingtype"
                                    value="standard"
                                    onChange={(e) => handleShippingType(e)}
                                  />{" "}
                                  Standard
                                </Label>
                              </FormGroup>
                              <FormGroup check className="mr-3">
                                <Label check className="shipping">
                                  <Input
                                    checked={detail.shippingtype === "express"}
                                    type="radio"
                                    name="shippingtype"
                                    value="express"
                                    onChange={(e) => handleShippingType(e)}
                                  />{" "}
                                  Express
                                </Label>
                              </FormGroup>
                              <FormGroup check>
                                <Label check className="shipping">
                                  <Input
                                    checked={detail.shippingtype === "schedule"}
                                    type="radio"
                                    name="shippingtype"
                                    value="schedule"
                                    onChange={(e) => handleShippingType(e)}
                                  />{" "}
                                  Schedule
                                </Label>
                              </FormGroup>
                            </div>
                          </div>
                          <div className="mb-4">
                            <div>
                              {detail.shippingtype === "schedule" ? (
                                <FormGroup>
                                  <Label>
                                    Schedule Date
                                    <Input
                                      type="date"
                                      name="schedule_time"
                                      value={detail.schedule_time}
                                      onChange={(e) => handleScheduleTime(e)}
                                    />{" "}
                                  </Label>
                                </FormGroup>
                              ) : (
                                ""
                              )}
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Contact Details</div>
                      </Col>
                      <Col lg="9">
                        <div>
                          <div className="form-row">
                            <FormGroup className="col-lg-4">
                              <label>Phone Number</label>
                              <Input
                                placeholder="Phone Number"
                                value={detail.phoneNumber}
                                onChange={(e) => {
                                  handleOnChange(e);
                                }}
                                type="tel"
                                name="phoneNumber"
                              />
                            </FormGroup>
                            <FormGroup className="col-lg-6">
                              <label>Email Address</label>
                              <Input
                                placeholder="Email Address"
                                defaultValue={detail.email}
                                readOnly
                                type="email"
                                name="email"
                              />
                            </FormGroup>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Extra Instructions</div>
                      </Col>
                      <Col lg="9">
                        <div>
                          <div className="form-row">
                            <FormGroup className="col-lg-10">
                              <Input
                                value={detail.extraInstruction}
                                onChange={(e) => {
                                  handleOnChange(e);
                                }}
                                placeholder="Anything else you want us to know?"
                                rows="3"
                                type="textarea"
                                name="extraInstruction"
                              />
                            </FormGroup>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Payment Method</div>
                      </Col>
                      <Col lg="9">
                        <div className="row">
                          <div className="col-lg-10">
                            {/* <Accordion allowZeroExpanded>
                            {paymentMethod.map((item) => (
                              <AccordionItem key={item.paymentTitle}>
                                <AccordionItemHeading>
                                  <AccordionItemButton>
                                    <div className="accordion-header">
                                      {item.paymentTitle}
                                    </div>
                                  </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel>
                                  <div className="accordion-content">
                                    {item.paymentView}
                                  </div>
                                </AccordionItemPanel>
                              </AccordionItem>
                            ))}
                          </Accordion> */}
                            <div className="discount-block">
                              <FormGroup className="row">
                                <Label sm="6">Apply discount code</Label>
                                <Col sm="6">
                                  <Input
                                    className="form-control-plaintext"
                                    value={detail.coupon}
                                    placeholder="BEIDNK24"
                                    type="text"
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    name="coupon"
                                  />
                                </Col>
                              </FormGroup>
                              {/* <div className="hint">
                              This code gives you N1500 off your total purchase
                            </div> */}
                            </div>
                            <div className="policy-form">
                              <FormGroup check>
                                <Label check>
                                  <Input
                                    type="checkbox"
                                    onChange={(e) =>
                                      handlePolicy(e.target.checked)
                                    }
                                    name="policy"
                                    defaultValue={detail.policy}
                                  />
                                  <span className="form-check-sign">
                                    <span className="check"></span>
                                  </span>
                                  By proceeding, you hereby agree to our
                                  Policies,{" "}
                                  <Link to="/terms-of-service">
                                    Terms and Conditions
                                  </Link>
                                  .{" "}
                                </Label>
                              </FormGroup>
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    {hidepaybutton ? (
                      ""
                    ) : (
                      <Row className="mb-4">
                        <Col lg="3" />
                        <Col lg="9">
                          <div>
                            <div className="form-row">
                              <div className="col-lg-10">
                                <Button
                                  className="bg-primary-gradient"
                                  color="primary"
                                  size="lg"
                                  block
                                  type="submit"
                                  disabled={loading}
                                >
                                  {loading ? (
                                    <i
                                      className="fa fa-spinner fa-spin"
                                      aria-hidden="true"
                                    ></i>
                                  ) : (
                                    "Pay"
                                  )}
                                </Button>
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                    )}
                  </Form>
                </Col>
                <Col lg="5">
                  {cartItems ? (
                    <div className="order-summary">
                      <div className="mb-3 order-name">
                        <Label>Order title</Label>
                        <Input
                          onChange={(e) => handleName(e.target.value)}
                          value={name}
                          placeholder="Provide your order title"
                        />
                      </div>
                      <div className="cart-block">
                        {cartItems.map((item) => (
                          <div className="cart-item-block" key={item.id}>
                            <img
                              alt="..."
                              className="product-img"
                              src={item.image}
                            />
                            <div style={{ flex: 1 }}>
                              <div className="d-flex justify-content-between">
                                <div>
                                  <p className="product-name">
                                    {item.product_name}
                                  </p>
                                  <p className="product-location">
                                    <i className="fa fa-map-marker-alt mr-2" />
                                    {item.store_name}
                                  </p>
                                </div>
                                <Button
                                  onClick={() => deleteCart(item.id)}
                                  className="btn-icon btn-link mt-0"
                                  color="danger"
                                  type="button"
                                  size="sm"
                                >
                                  <i className="fa fa-trash-alt" />
                                </Button>
                              </div>

                              <div className="d-flex align-items-center justify-content-between mt-2">
                                <p className="product-price h6">
                                  {currency(item.price)}
                                </p>
                                <div className="action-buttons d-flex align-items-center justify-content-between">
                                  <Button
                                    onClick={() => handleSubtract(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    disabled={item.quantity === 1}
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="now-ui-icons ui-1_simple-delete" />
                                  </Button>
                                  <span className="product-amount">
                                    {item.quantity}
                                  </span>
                                  <Button
                                    onClick={() => handleAdd(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="now-ui-icons ui-1_simple-add" />
                                  </Button>
                                </div>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>

                      <div className="mt-5">
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Total weight</p>
                          <p className="font-weight-bold mb-1">{weight}kg</p>
                        </div>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Fulfilment fee</p>
                          <p className="font-weight-bold mb-1">
                            {currency(fulfilment)}
                          </p>
                        </div>
                        {/* <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Commission</p>
                          <p className="font-weight-bold mb-1">
                            {" "}
                            {currency(commission)}
                          </p>
                        </div> */}
                      </div>

                      <div className="d-flex align-items-center justify-content-between total-block">
                        <p className="m-0">Total amount</p>
                        <h5 className="font-weight-bold m-0">
                          {currency(total)}
                        </h5>
                      </div>
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
              </Row>
            ) : (
              <div>
                {showcart ? (
                  <div>
                    <div className="mb-3">
                      {" "}
                      <small className="" onClick={() => handleCart()}>
                        {" "}
                        <i
                          className="fa fa-arrow-left"
                          aria-hidden="true"
                        ></i>{" "}
                        Back
                      </small>
                    </div>
                    <Form
                      className="checkout-form"
                      onSubmit={(e) => handleSubmit(e)}
                    >
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Personal Details</div>
                        </Col>
                        <Col lg="9">
                          <div className="form-row">
                            <FormGroup className="col-lg-5">
                              <label>Surname</label>
                              <Input
                                placeholder="Surname"
                                defaultValue={detail.surname}
                                readOnly
                                type="text"
                                name="surname"
                              />
                            </FormGroup>
                            <FormGroup className="col-lg-5">
                              <label>First Name</label>
                              <Input
                                placeholder="First Name"
                                defaultValue={detail.firstName}
                                readOnly
                                type="text"
                                name="firstName"
                              />
                            </FormGroup>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Shipping Details</div>
                        </Col>
                        <Col lg="9">
                          <div>
                            <div>
                              <FormGroup check>
                                <Label check className="delivery">
                                  <Input
                                    type="radio"
                                    name="deliverymethod"
                                    checked={detail.deliverymethod === "home"}
                                    value="home"
                                    onChange={(e) => handleDeliveryType(e)}
                                  />{" "}
                                  <span>Have it delivered to your home</span>
                                </Label>
                              </FormGroup>

                              <div
                                className="form-row"
                                style={{
                                  opacity:
                                    detail.deliverymethod === "pickup"
                                      ? 0.4
                                      : "",
                                }}
                              >
                                <FormGroup className="col-lg-10">
                                  <label>Address</label>
                                  <Input
                                    placeholder="Address"
                                    value={detail.address}
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    type="text"
                                    name="address"
                                  />
                                </FormGroup>
                                <FormGroup className="col-lg-5">
                                  <label>State</label>
                                  <Input
                                    placeholder="State"
                                    value={detail.state}
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    type="select"
                                    name="state"
                                  >
                                    <option value="">Choose...</option>
                                    {states.map((item) => (
                                      <option
                                        value={item.state.name}
                                        key={item.state.id}
                                      >
                                        {item.state.name}
                                      </option>
                                    ))}
                                  </Input>
                                </FormGroup>

                                <FormGroup className="col-lg-5">
                                  <label>City</label>
                                  <Input
                                    placeholder="City"
                                    value={detail.city}
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    type="select"
                                    name="city"
                                  >
                                    <option value="">Choose...</option>
                                    {cities.map((item) => (
                                      <option value={item.name} key={item.id}>
                                        {item.name}
                                      </option>
                                    ))}
                                  </Input>
                                </FormGroup>
                              </div>
                            </div>

                            <div className="mb-4">
                              <FormGroup check>
                                <Label check className="delivery">
                                  <Input
                                    type="radio"
                                    name="deliverymethod"
                                    checked={detail.deliverymethod === "pickup"}
                                    value="pickup"
                                    onChange={(e) => handleDeliveryType(e)}
                                  />{" "}
                                  <span>
                                    Or have it dropped at a pick up location
                                  </span>
                                </Label>
                              </FormGroup>
                              <div
                                style={{
                                  opacity:
                                    detail.deliverymethod === "home" ? 0.4 : "",
                                }}
                              >
                                <div className="form-row">
                                  <FormGroup className="col-lg-10">
                                    <label>
                                      Select pick up point nearest to you
                                    </label>
                                    <Input
                                      placeholder="PickUp Point"
                                      value={detail.pickupPoint}
                                      onChange={(e) => {
                                        handleOnChange(e);
                                      }}
                                      name="pickupPoint"
                                      type="select"
                                    >
                                      <option defaultValue="">
                                        4, Maijala street, Ikeja GRA, Ikeja,
                                        Lagos
                                      </option>
                                    </Input>
                                  </FormGroup>
                                </div>
                              </div>
                            </div>

                            <div className="mb-4">
                              <p className="mb-0">Shipping type</p>
                              <div className="d-flex">
                                <FormGroup check className="mr-3">
                                  <Label check className="shipping">
                                    <Input
                                      checked={
                                        detail.shippingtype === "standard"
                                      }
                                      type="radio"
                                      name="shippingtype"
                                      value="standard"
                                      onChange={(e) => handleShippingType(e)}
                                    />{" "}
                                    Standard
                                  </Label>
                                </FormGroup>
                                <FormGroup check className="mr-3">
                                  <Label check className="shipping">
                                    <Input
                                      checked={
                                        detail.shippingtype === "express"
                                      }
                                      type="radio"
                                      name="shippingtype"
                                      value="express"
                                      onChange={(e) => handleShippingType(e)}
                                    />{" "}
                                    Express
                                  </Label>
                                </FormGroup>
                                <FormGroup check>
                                  <Label check className="shipping">
                                    <Input
                                      checked={
                                        detail.shippingtype === "schedule"
                                      }
                                      type="radio"
                                      name="shippingtype"
                                      value="schedule"
                                      onChange={(e) => handleShippingType(e)}
                                    />{" "}
                                    Schedule
                                  </Label>
                                </FormGroup>
                              </div>
                            </div>
                            <div className="mb-4">
                              <div>
                                {detail.shippingtype === "schedule" ? (
                                  <FormGroup>
                                    <Label>
                                      Schedule Date
                                      <Input
                                        type="date"
                                        name="schedule_time"
                                        value={detail.schedule_time}
                                        onChange={(e) => handleScheduleTime(e)}
                                      />{" "}
                                    </Label>
                                  </FormGroup>
                                ) : (
                                  ""
                                )}
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Contact Details</div>
                        </Col>
                        <Col lg="9">
                          <div>
                            <div className="form-row">
                              <FormGroup className="col-lg-4">
                                <label>Phone Number</label>
                                <Input
                                  placeholder="Phone Number"
                                  value={detail.phoneNumber}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  type="tel"
                                  name="phoneNumber"
                                />
                              </FormGroup>
                              <FormGroup className="col-lg-6">
                                <label>Email Address</label>
                                <Input
                                  placeholder="Email Address"
                                  defaultValue={detail.email}
                                  readOnly
                                  type="email"
                                  name="email"
                                />
                              </FormGroup>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Extra Instructions</div>
                        </Col>
                        <Col lg="9">
                          <div>
                            <div className="form-row">
                              <FormGroup className="col-lg-10">
                                <Input
                                  value={detail.extraInstruction}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  placeholder="Anything else you want us to know?"
                                  rows="3"
                                  type="textarea"
                                  name="extraInstruction"
                                />
                              </FormGroup>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Payment Method</div>
                        </Col>
                        <Col lg="9">
                          <div className="row">
                            <div className="col-lg-10">
                              {/* <Accordion allowZeroExpanded>
                            {paymentMethod.map((item) => (
                              <AccordionItem key={item.paymentTitle}>
                                <AccordionItemHeading>
                                  <AccordionItemButton>
                                    <div className="accordion-header">
                                      {item.paymentTitle}
                                    </div>
                                  </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel>
                                  <div className="accordion-content">
                                    {item.paymentView}
                                  </div>
                                </AccordionItemPanel>
                              </AccordionItem>
                            ))}
                          </Accordion> */}
                              <div className="discount-block">
                                <FormGroup className="row">
                                  <Label sm="6">Apply discount code</Label>
                                  <Col sm="6">
                                    <Input
                                      className="form-control-plaintext"
                                      value={detail.coupon}
                                      placeholder="BEIDNK24"
                                      type="text"
                                      onChange={(e) => {
                                        handleOnChange(e);
                                      }}
                                      name="coupon"
                                    />
                                  </Col>
                                </FormGroup>
                                {/* <div className="hint">
                              This code gives you N1500 off your total purchase
                            </div> */}
                              </div>
                              <div className="policy-form">
                                <FormGroup check>
                                  <Label check>
                                    <Input
                                      type="checkbox"
                                      onChange={(e) =>
                                        handlePolicy(e.target.checked)
                                      }
                                      name="policy"
                                      defaultValue={detail.policy}
                                    />
                                    <span className="form-check-sign">
                                      <span className="check"></span>
                                    </span>
                                    By proceeding, you hereby agree to our
                                    Policies,{" "}
                                    <Link to="/terms-of-service">
                                      Terms and Conditions
                                    </Link>
                                    .{" "}
                                  </Label>
                                </FormGroup>
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      {hidepaybutton ? (
                        ""
                      ) : (
                        <Row className="mb-4">
                          <Col lg="3" />
                          <Col lg="9">
                            <div>
                              <div className="form-row">
                                <div className="col-lg-10">
                                  <Button
                                    className="bg-primary-gradient"
                                    color="primary"
                                    size="lg"
                                    block
                                    type="submit"
                                    disabled={loading}
                                  >
                                    {loading ? (
                                      <i
                                        className="fa fa-spinner fa-spin"
                                        aria-hidden="true"
                                      ></i>
                                    ) : (
                                      "Pay"
                                    )}
                                  </Button>
                                </div>
                              </div>
                            </div>
                          </Col>
                        </Row>
                      )}
                    </Form>
                  </div>
                ) : (
                  <div>
                    {cartItems.length ? (
                      <div className="order-summary">
                        <div className="mb-3 order-name">
                          <Label>Order title</Label>
                          <Input
                            onChange={(e) => handleName(e.target.value)}
                            value={name}
                            placeholder="Provide a title for this order"
                          />
                        </div>
                        <div className="cart-block">
                          {cartItems.map((item) => (
                            <div className="cart-item-block" key={item.id}>
                              <img
                                alt="..."
                                className="product-img"
                                src={item.image}
                              />
                              <div style={{ flex: 1 }}>
                                <div className="d-flex justify-content-between">
                                  <div>
                                    <p className="product-name">
                                      {item.product_name}
                                    </p>
                                    <p className="product-location">
                                      <i className="fa fa-map-marker-alt mr-2" />
                                      {item.store_name}
                                    </p>
                                  </div>
                                  <Button
                                    onClick={() => deleteCart(item.id)}
                                    className="btn-icon btn-link mt-0"
                                    color="danger"
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="fa fa-trash-alt" />
                                  </Button>
                                </div>

                                <div className="d-flex align-items-center justify-content-between mt-2">
                                  <p className="product-price h6">
                                    {currency(
                                      item.sales_price
                                        ? item.sales_price
                                        : item.price
                                    )}
                                  </p>
                                  <div className="action-buttons d-flex align-items-center justify-content-between">
                                    <Button
                                      onClick={() => handleSubtract(item.id)}
                                      className="btn-icon m-0 btn-round"
                                      color=""
                                      disabled={item.quantity === 1}
                                      type="button"
                                      size="sm"
                                    >
                                      <i className="now-ui-icons ui-1_simple-delete" />
                                    </Button>
                                    <span className="product-amount">
                                      {item.quantity}
                                    </span>
                                    <Button
                                      onClick={() => handleAdd(item.id)}
                                      className="btn-icon m-0 btn-round"
                                      color=""
                                      type="button"
                                      size="sm"
                                    >
                                      <i className="now-ui-icons ui-1_simple-add" />
                                    </Button>
                                  </div>
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>

                        <div className="mt-5">
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Shipping fee</p>
                            <p className="font-weight-bold mb-1">
                              {currency(shipping)}
                            </p>
                          </div>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Commission</p>
                            <p className="font-weight-bold mb-1">
                              {" "}
                              {currency(commission)}
                            </p>
                          </div>
                        </div>

                        <div className="d-flex align-items-center justify-content-between total-block">
                          <p className="m-0">Total amount</p>
                          <h5 className="font-weight-bold m-0">
                            {currency(total)}
                          </h5>
                        </div>
                      </div>
                    ) : (
                      <div className="text-center">No item in cart</div>
                    )}

                    <Button
                      onClick={() => handleCart()}
                      type="button"
                      color="primary"
                      block
                    >
                      Proceed
                    </Button>
                  </div>
                )}
              </div>
            )}
          </Container>
          <Modal
            isOpen={modal}
            toggle={toggle}
            centered
            className="section-product-details-modal"
          >
            <ModalBody className=" position-relative">
              <div>
                <div className="p-5">
                  <div className=" text-center">
                    <i
                      className="fa fa-check-circle fa-4x text-success"
                      aria-hidden="true"
                    ></i>{" "}
                    <br />
                    <strong>Payment successful</strong> <br />
                    <small>Your receipt will be sent to you via mail</small>
                  </div>
                </div>

                <img alt="red" src={red} className="modal-red" />
                <img alt="yellow" src={yellow} className="modal-yellow" />
              </div>
            </ModalBody>
          </Modal>
        </section>

        <DefaultFooter />
      </div>
    </>
  );
}

export default CheckOut;
