import {
  AddToCart,
  LoadCart,
  UpdateCart,
  ClearCart,
  RemoveCart,
  LoadTotal,
} from "../services/cartservice";

import * as ActionTypes from "../constants/action-types";
import { toast } from "react-toastify";

export const LoadCartAction = () => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.LOADING });
    LoadCart().then(
      (res) => {
        dispatch({ type: ActionTypes.GET_CART, res });
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const GetTotalAction = () => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.LOADING });
    LoadTotal().then(
      (res) => {
        dispatch({ type: ActionTypes.GET_TOTAL, res });
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const AddCartAction = (data) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.LOADING });
    AddToCart(data).then(
      ({ status, data }) => {
        if (status === "success") {
          toast.success("Added to cart");

          dispatch({ type: ActionTypes.ADD_TO_CART, data });
        }
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const UpdateCartAction = (id, items) => {
  return (dispatch) => {
    UpdateCart(id, items).then(
      (res) => {
        dispatch({ type: ActionTypes.UPDATE_CART, res, id });
        LoadTotal().then(
          (res) => {
            dispatch({ type: ActionTypes.GET_TOTAL, res });
          },
          (error) => {
            dispatch({ type: ActionTypes.CODE_ERROR, error });
          }
        );
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const RemoveCartAction = (id) => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.LOADING });
    RemoveCart(id).then(
      (res) => {
        dispatch({ type: ActionTypes.REMOVE_CART, res, id });
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const ClearCartAction = () => {
  return (dispatch) => {
    dispatch({ type: ActionTypes.LOADING });
    ClearCart().then(
      (res) => {
        if (res.hasOwnProperty("success") && res.success === true) {
          dispatch({ type: ActionTypes.CLEAR_CART, res });
        } else if (res.hasOwnProperty("success") && res.success === false) {
          dispatch({ type: ActionTypes.CLEAR_CART_ERROR, res });
        }
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};
